* {margin:0; padding:0; box-sizing: border-box}

button {
  outline: none;
  border: 0;
  cursor: pointer;
}

.navbarTitle {
  font-size: 2rem;
  padding: 1rem;
  background-color: #7FD1AE;
}

.navbarLogo {
  color:#022601; 
  margin-right: 0.5rem;
}
.navbarCount {
  display: inline-block;
  text-align: center;
  font-size: 2rem;
  width: 2.5rem;
  height: 2.5rem;
  line-height: 2.4rem;
  margin-left: 0.5rem;
  background-color: #4F4537;
  color: white;
  border-radius: 50%;
}

.addInput {
  font-size: 2rem;
  margin: 1rem;
  margin-right: 0.2rem;
  margin-left: 1rem;
  text-indent: 0.5rem;
}
.addButton {
  height: 100%;
  font-size: 1.6rem;
  padding: 0.5rem 0.5rem 0.2rem 0.5rem;
  background-color: #F29F8D;
  border-radius: 0.5rem;
  color: white;
}

.habitContainer {
  font-size: 2.5rem;
  padding: 0.5rem;
  list-style: none;
  padding-left: 1.5rem;
  /* background-color: #F2EADF; */
}
.habitName {
  display: inline-block;
  width: 10.5rem;
  height: 5rem;
  font-size: 3rem;
}
.habitCount {
  display: inline-block;
  text-align: center;
  font-size: 2.5rem;
  width: 3rem;
  height: 3rem;
  line-height: 2.9rem;
  margin-left: 0.5rem;
  margin-right: 2rem;
  background-color: #BEA887;
  border-radius: 50%;
  color: white;
}
.habitButton {
  font-size: 2.6rem;
  margin: 0 0.2rem;
  background-color: transparent;
}

.habitButton:hover {
  opacity: 0.8;
}

.habitIncrease, .habitDecrease {
  color: #022601;
}
.habitDelete {
  color: #F29F8D;
}

.habitsReset {
  font-size: 1.5rem;
  padding: 0.5rem 1rem;
  margin: 0.5rem;
  background-color: #F29F8D;
  color: white;
  border-radius: 0.5rem;
}